import { NewsFeed, NewsDetail} from '../types';

export class Api {
    ajax: XMLHttpRequest;
    url: string;
  
    constructor(url: string) {
      this.ajax = new XMLHttpRequest;
      this.url = url;
    }
  
    getRequest<AjaxResponse>(): AjaxResponse {
      this.ajax.open('GET', this.url, false); // ë™ê¸°ì ìœ¼ë¡œ ì²˜ë¦¬í•˜ê² ë‹¤.
      this.ajax.send();
  
      return JSON.parse(this.ajax.response);
    }
  }
  
  // ì˜ì‚¬ì½”ë“œ : ì „ì²´ì ìœ¼ë¡œ íë¦„ë§Œ ì•Œê¸° ìœ„í•´ì„œ ë¬¸ë²•ë„ ì‹ ê²½ì“°ì§€ ì•Šê³  ì½”ë”©í•˜ëŠ” ê²ƒ
  // javascript ë¯¹ìŠ¤ì¸ 
  // ë¯¹ìŠ¤ë“œì¸ì„ ì“°ëŠ” ì´ìœ 
  // 1. ë‹¤ì¤‘ ìƒì† ë¶ˆê°€
  // 2. ì—®ëŠ” ê´€ê³„ë¥¼ ì½”ë“œë¡œ ëª…ì‹œí•´ ì¤˜ì•¼ í•¨.
  function applyApiMixins(targetClass: any, baseClass: any[]): void {
    baseClass.forEach(baseClass => {
      Object.getOwnPropertyNames(baseClass.prototype).forEach(name => {
        const descriptor = Object.getOwnPropertyDescriptor(baseClass.prototype, name);
  
        if (descriptor) {
          Object.defineProperty(targetClass.prototype, name, descriptor);
        }
      });
    });
  }
  
  export class NewsFeedApi extends Api {
    getData(): NewsFeed[] {
      return this.getRequest<NewsFeed[]>();
    }
  }
  
  export class NewsDetailApi extends Api {
    getData(): NewsDetail {
      return this.getRequest<NewsDetail>();
  
    }
  }